import React, { useState } from "react";
import {
    Box,
    Button,
    FormControl,
    InputLabel,
    MenuItem,
    Select,
    TextField,
    Typography,
    Paper,
    Stack,
} from "@mui/material";
import { Document, Packer, Paragraph, TextRun } from "docx";
import { saveAs } from "file-saver";
import Span from "./Span";

// ======   ( 4) ======
const variants = [
    { id: 1, m: 640, n: 480, V: 300.0 },
    { id: 2, m: 1600, n: 900, V: 879.0 },
    { id: 3, m: 320, n: 240, V: 150.0 },
    { id: 4, m: 1200, n: 900, V: 791.1 },
    { id: 5, m: 512, n: 256, V: 256.0 },
    { id: 6, m: 128, n: 130, V: 32.5 },
    { id: 7, m: 600, n: 1000, V: 659.2 },
    { id: 8, m: 1600, n: 1200, V: 939.0 },
    { id: 9, m: 512, n: 512, V: 192.0 },
    { id: 10, m: 1024, n: 1024, V: 512.0 },
    { id: 11, m: 1200, n: 768, V: 900.0 },
    { id: 12, m: 160, n: 128, V: 40.0 },
    { id: 13, m: 250, n: 300, V: 137.4 },
    { id: 14, m: 800, n: 600, V: 586.0 },
    { id: 15, m: 600, n: 400, V: 175.8 },
    { id: 16, m: 128, n: 320, V: 40.0 },
    { id: 17, m: 768, n: 602, V: 451.5 },
    { id: 18, m: 640, n: 321, V: 376.2 },
    { id: 19, m: 640, n: 320, V: 400.0 },
    { id: 20, m: 512, n: 301, V: 131.7 },
    { id: 21, m: 640, n: 384, V: 390.0 },
    { id: 22, m: 256, n: 192, V: 18.0 },
    { id: 23, m: 1200, n: 1024, V: 900.0 },
    { id: 24, m: 600, n: 400, V: 293.0 },
    { id: 25, m: 1024, n: 600, V: 600.0 },
];

// ======  ======
const CounterColors4 = () => {
    const [variant, setVariant] = useState(1);
    const [data, setData] = useState(variants[0]);
    const [result, setResult] = useState(null);

    const handleVariantChange = (event) => {
        const selected = variants.find((v) => v.id === Number(event.target.value));
        if (selected) {
            setVariant(selected.id);
            setData(selected);
            setResult(null);
        }
    };

    const handleChange = (field, value) => {
        setData((prev) => ({ ...prev, [field]: Number(value) }));
    };

    const calculate = () => {
        const { m, n, V } = data;

        // 1)   
        const V_bytes = V * 1024; //  -> 
        const V_bits = V_bytes * 8; //  -> 

        // 2)  
        const pixels = m * n;

        // 3) code (/)    
        const code = V_bits / pixels; //    (  )

        // 4)  2^code (  ,   )
        const pow2 = Math.pow(2, code);

        // 5)         2^code
        const k = Math.floor(pow2);

        // 6)   :   code ( )   ( )
        const codeInt = Math.floor(code);
        const codeFrac = code - codeInt;

        setResult({
            V_bytes,
            V_bits,
            pixels,
            code,
            codeInt,
            codeFrac,
            pow2,
            k,
        });
    };

    //  Word   
    const generateWord = async () => {
        if (!result) return;
        const { m, n, V } = data;
        const {
            V_bytes,
            V_bits,
            pixels,
            code,
            codeInt,
            codeFrac,
            pow2,
            k,
        } = result;

        const doc = new Document({
            sections: [
                {
                    children: [
                        new Paragraph({
                            children: [
                                new TextRun({ text: " 4.   ", bold: true }),
                            ],
                        }),
                        new Paragraph(" "),
                        new Paragraph(" :"),
                        new Paragraph(` : ${m}  ${n} `),
                        new Paragraph(` : ${V} `),
                        new Paragraph(" "),
                        new Paragraph(" 1.      :"),
                        new Paragraph(`V_bytes = ${V}  1024 = ${V_bytes.toFixed(0)} `),
                        new Paragraph(`V_bits = V_bytes  8 = ${V_bits.toFixed(0)} `),
                        new Paragraph(" "),
                        new Paragraph(" 2. :"),
                        new Paragraph(`pixels = m  n = ${m}  ${n} = ${pixels}`),
                        new Paragraph(" "),
                        new Paragraph(" 3.    (code):"),
                        new Paragraph(`code = V_bits / pixels = ${V_bits.toFixed(0)} / ${pixels} = ${code.toFixed(6)} (/)`),
                        new Paragraph(" "),
                        new Paragraph(" 4.    = 2^code:"),
                        new Paragraph(`2^code = 2^${code.toFixed(6)}  ${pow2.toFixed(6)}`),
                        new Paragraph(" "),
                        new Paragraph(" 5.      ( ):"),
                        new Paragraph(`k = floor(2^code) = ${k}`),
                    ],
                },
            ],
        });

        const blob = await Packer.toBlob(doc);
        saveAs(blob, `4_${variant}.docx`);
    };

    return (
        <Paper sx={{ p: 3, maxWidth: 760, margin: "0 auto" }}>
            <Typography variant="h5" gutterBottom>
                 4.    
            </Typography>

            <Typography variant="body2" sx={{ mb: 2 }}>
                      PDF.
                 : <code>/mnt/data/  4 .pdf</code>.
            </Typography>

            {/*   */}
            <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel></InputLabel>
                <Select value={variant} label="" onChange={handleVariantChange}>
                    {variants.map((v) => (
                        <MenuItem key={v.id} value={v.id}>
                             {v.id}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>

            {/*  */}
            <Stack spacing={2}>
                <TextField label="m ()" type="number" value={data.m} onChange={(e) => handleChange("m", e.target.value)} />
                <TextField label="n ()" type="number" value={data.n} onChange={(e) => handleChange("n", e.target.value)} />
                <TextField label="V ()" type="number" value={data.V} onChange={(e) => handleChange("V", e.target.value)} />
            </Stack>

            <Button variant="contained" sx={{ mt: 3 }} onClick={calculate}>
                
            </Button>

            {result && (
                <Box sx={{ mt: 3 }}>
                    <Typography variant="h6"></Typography>

                    <Typography sx={{ mt: 1 }}>
                        <strong> 1   :</strong><br />
                        V_bytes = {data.V}  1024 = {result.V_bytes.toFixed(0)} <br />
                        V_bits = V_bytes  8 = {result.V_bits.toFixed(0)} 
                    </Typography>

                    <Typography sx={{ mt: 1 }}>
                        <strong> 2   :</strong><br />
                        pixels = m  n = {data.m}  {data.n} = {result.pixels}
                    </Typography>

                    <Typography sx={{ mt: 1 }}>
                        <strong> 3     (code):</strong><br />
                        code = V_bits / pixels = {result.V_bits.toFixed(0)} / {result.pixels} = {result.code.toFixed(6)} /
                        <br />
                        ( : {result.codeInt},  : {result.codeFrac.toFixed(6)})
                    </Typography>

                    <Typography sx={{ mt: 1 }}>
                        <strong> 4    :</strong><br />
                        2<Span top>{result.code.toFixed(6)}</Span> = {result.pow2.toFixed(6)}
                        {Number.isInteger(result.code) ? (
                            <><br /> code  ({result.codeInt}),   2^{result.codeInt} = {result.pow2.toFixed(0)}</>
                        ) : null}
                    </Typography>

                    <Typography sx={{ mt: 1 }}>
                        <strong> 5     :</strong><br />
                        k = floor(2^code) = floor({result.pow2.toFixed(6)}) = <strong>{result.k.toLocaleString()}</strong>
                    </Typography>

                    {/* <Typography sx={{ mt: 2, color: "text.secondary" }}>
                        :  ,       
                          ;     PDF.
                    </Typography> */}

                    <Button variant="outlined" sx={{ mt: 2 }} onClick={generateWord}>
                         Word
                    </Button>
                </Box>
            )}
        </Paper>
    );
};

export default CounterColors4;
